h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--main-text-color);
  background-color: var(--background-color);
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.625;
  position: relative;
}

body.modal-open {
  height: 100vh;
  overflow-y: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list, .social-list, .hero-contacts__list, .menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link, .link-tel, .social-list__href, .hero-contacts__link, .hero__link {
  color: inherit;
  padding: 15px 0;
  text-decoration: none;
  display: inline-block;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 320px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.section, .contacts, .about {
  background-color: var(--background-color);
  padding-top: 60px;
  padding-bottom: 60px;
}

a.anchor {
  color: red;
  visibility: hidden;
  display: block;
  position: relative;
  top: -86px;
}

@media screen and (min-width: 768px) {
  a.anchor {
    top: -102px;
  }
}

@media screen and (min-width: 1200px) {
  a.anchor {
    top: -111px;
  }
}

.header {
  z-index: 100;
  background-color: rgba(0, 0, 0, 0);
  padding: 8px 20px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 16px 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding: 20px 56px;
  }
}

.js-no-transparency {
  -webkit-backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(209, 130, 200, .314);
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: ease;
}

.header-nav {
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: relative;
}

.header--logo {
  margin-right: 61px;
}

@media screen and (min-width: 1200px) {
  .header--logo {
    margin-right: 0;
    padding-left: 0;
  }
}

.header__list {
  display: none;
}

@media screen and (min-width: 1200px) {
  .header__list {
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
}

.menu__link {
  color: inherit;
  padding: 15px 0;
  text-decoration: none;
  display: inline-block;
}

.menu__link:hover, .menu__link:focus {
  color: var(--accent-main-color);
  cursor: pointer;
}

.current {
  color: var(--accent-main-color);
}

.buy-btn {
  display: none;
}

@media screen and (min-width: 768px) {
