.advantages {
  background-color: #fff5f6;
  padding-bottom: 60px;
}

.advanteges__list {
    @media screen and (min-width: 768px) {
        display: flex;    
    }
  
}
.advanteges__item:not(:last-child) {

    @media screen and (max-width: 767px) {
        margin-bottom: 32px;
    }
    @media screen and (min-width: 768px) {
        margin-right: 25px;
    }
    @media screen and (min-width: 1200px) {
        margin-right: 115px;
    }
}
.advanteges__item::before {
    content: "";
    display: flex;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 16px;
    @media screen and (min-width: 768px) {
        margin-bottom: 36px;
    }
}
.advanteges__item--first::before {
  width: 33px;
  background-image: url('../images/advanteges/first.png');
}

.advanteges__item--second::before {
  content: '';
  width: 50px;
  background-image: url('../images/advanteges/second.png');
}

.advanteges__item--last::before {
  content: '';
  width: 44px;
  background-image: url('../images/advanteges/last.png');
}

.advanteges__item-count {
    display: flex;
    width: 98px;
    height: 56px;
    font-family: 'Titan One';
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(1.5);
    cursor: pointer;
  }
    margin-bottom: 4px;
    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 1.15;
        width: 147px;
        margin-bottom: 13px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 58px;
        line-height: 1.14;
        width: 147px;
        margin-bottom: 16px;
    }
}

.advanteges__item-text {
    max-width: 230px;
    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 12px;
    line-height: 1.67;
    color: #000000;
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(1.5);
    cursor: pointer;
  }
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 1.71;
        max-width: 159px;
        height: 67px;
    }
    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 1.75;
        max-width: 288px;
    }
}
